// @flow

import React, {type Node} from 'react';
import {
    useFragment, type PreloadedQuery, usePreloadedQuery, usePaginationFragment
} from 'react-relay';

import Typography from '@mui/material/Typography';

import FilmDetailsPageQuery, {
    SummaryFragment,
    CharacterConnectionFragment
} from 'graphql-query/film-details-page';
import {type filmDetailsPageQuery as filmDetailsPageQueryType} from 'graphql-query/__generated__/filmDetailsPageQuery.graphql';
import {type filmDetailsPage_film_SummaryFragment$key} from 'graphql-query/__generated__/filmDetailsPage_film_SummaryFragment.graphql';
import {type filmDetailsPage_film_characterConnectionFragment$key} from 'graphql-query/__generated__/filmDetailsPage_film_CharacterConnectionFragment.graphql';
import {type filmDetailsPage_film_characterConnection_paginationQuery} from 'graphql-query/__generated__/filmDetailsPage_film_characterConnection_paginationQuery.graphql'


import {
    FilmImage,
    ImageAndSummary,
    SummaryDiv,
    SummaryFieldWrapper
} from './styled';

import svgImageByEpisode from 'pages/images/index';
import LoadMoreTable from "./load-more-table";

type SummaryFieldProps = {
    fieldId: string,
    fieldName: string,
    fieldValue: ?string | ?number,
};

const SummaryField = ({fieldId, fieldName, fieldValue}: SummaryFieldProps) => {
    return <SummaryFieldWrapper>
        <Typography component="div">{fieldName}:</Typography>
        <Typography component="div">{fieldValue}</Typography>
    </SummaryFieldWrapper>;
};


type CharactersProps = {
    fragmentRef: filmDetailsPage_film_characterConnectionFragment$key
};

const Characters = ({fragmentRef}: CharactersProps) => {
    const {
        data,
        loadNext,
        hasNext
    } = usePaginationFragment<filmDetailsPage_film_characterConnection_paginationQuery, _>(CharacterConnectionFragment, fragmentRef)

    const header = ['Name', 'Gender', 'Skin Color', 'Eye Color'];
    const rows = data.characterConnection?.edges?.map(e => [e?.node?.name, e?.node?.gender, e?.node?.skinColor, e?.node?.eyeColor]);
    return <LoadMoreTable rows={rows ? rows : []} header={header}/>
};

type SummaryProps = {
    fragmentRef: filmDetailsPage_film_SummaryFragment$key
};

const Summary = ({fragmentRef}: SummaryProps) => {
    const film = useFragment(SummaryFragment, fragmentRef)
    return (
        <SummaryDiv>
            <SummaryField fieldId='episodeId' fieldName='Episode' fieldValue={film.episodeID}/>
            <SummaryField fieldId='director' fieldName='Director' fieldValue={film?.director}/>
            <SummaryField fieldId='producers' fieldName='Producers'
                          fieldValue={(film?.producers ?? []).join(', ')}/>
            <SummaryField fieldId='releaseDate' fieldName='Release Date' fieldValue={film?.releaseDate}/>
            <Typography component="div">{film?.openingCrawl}</Typography>
        </SummaryDiv>
    );
}

type Props = {
    queryRef: PreloadedQuery<filmDetailsPageQueryType>,
};

const FilmDetailsPage = ({queryRef}: Props): Node => {

    const {film} = usePreloadedQuery<filmDetailsPageQueryType>(FilmDetailsPageQuery, queryRef)


    return (
        <>
            {film && <div>
                <ImageAndSummary>
                    <FilmImage src={svgImageByEpisode(film.id ?? "")}/>
                    <Summary fragmentRef={film}/>
                </ImageAndSummary>
                <div>
                    <Characters fragmentRef={film}/>
                </div>
            </div>}


        </>
    );
};

export default FilmDetailsPage;