// @flow

import React, {type Node} from 'react';
import {
    usePreloadedQuery,
    type PreloadedQuery
} from 'react-relay';

import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';

import {Film, Films} from './styled';

import svgImageByEpisode from 'pages/images/index';

import {type homePageQuery as homePageQueryType} from 'graphql-query/__generated__/homePageQuery.graphql';

import HomePageQuery from 'graphql-query/home-page';

type MovieCardProps = {
    id: string,
    episodeID: number,
    title: string,
    openingCrawl: string,
    onDetailsClick: (id: string) => void,
};

const MovieCard = ({id, episodeID, title, openingCrawl, onDetailsClick}: MovieCardProps) => {
    return <Film id={id}>
        <Card variant="outlined"
              sx={{
                  maxWidth: 500,
                  maxHeight: 600
              }}>
            <CardMedia
                component="img"
                height="140"
                image={svgImageByEpisode(id)}
                alt="green iguana"
            />
            <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                    Episode {episodeID}: {title}
                </Typography>
                <Typography noWrap={true} variant="body2" color="text.secondary">{openingCrawl}</Typography>
            </CardContent>
            <CardActions>
                <Button size="small" onClick={() => onDetailsClick(id)}>Details</Button>
            </CardActions>
        </Card>
    </Film>;
};

type Props = {
    onDetailsClick: (id: string) => void,
    queryRef: PreloadedQuery<homePageQueryType>,
};

const HomePage = ({queryRef, onDetailsClick}: Props): Node => {

    const {allFilms} = usePreloadedQuery<homePageQueryType>(HomePageQuery,
        queryRef
    );

    const films = allFilms?.films;

    return films && films.length > 0 ? <Films>
        {films.map(film => film ? <MovieCard
                key={film.id}
                id={film.id}
                episodeID={film.episodeID ?? 0}
                openingCrawl={film.openingCrawl ?? ""}
                title={film.title ?? ""}
                onDetailsClick={onDetailsClick}
            /> : null
        )}
    </Films> : null;
};

export default HomePage;