// @flow


import React, {Suspense, useCallback, useReducer, useState} from "react";
import './App.css';

import {
    RelayEnvironmentProvider,
    loadQuery,
    usePreloadedQuery,
    type PreloadedQuery
} from 'react-relay';

import RelayEnvironment from 'relay-environment/RelayEnvironment';

import Scaffolding from "app-scaffolding/index";
import HomePage from 'pages/home-page/index';
import FilmDetailsPage from 'pages/film-details-page/index';

import BreadcrumbsNavigation, {type NavItemProps} from "navigation/index";

import HomePageQuery, {type homePageQuery as homePageQueryType} from 'graphql-query/__generated__/homePageQuery.graphql';
import FilmDetailsPageQuery, {type filmDetailsPageQuery as filmDetailsPageQueryType} from "./graphql-query/__generated__/filmDetailsPageQuery.graphql";
import type {Node} from "react";
import {useQueryLoader} from "react-relay/hooks";


const preloadedHomePageQueryRef = loadQuery<homePageQueryType, void>(RelayEnvironment, HomePageQuery, {}, null, null);

type Page = 'HomePage' | 'FilmDetailsPage';

const getNavigationItems = (page: Page) => {
    const items: Array<NavItemProps> = [];
    if (page == 'HomePage') {
        items.push(
            {
                displayText: 'Home',
                isLast: true
            }
        );
    } else {
        items.push(
            {
                displayText: 'Home',
                isLast: false
            }
        );
        items.push(
            {
                displayText: 'Details',
                isLast: true
            }
        );
    }
    return items;
};

const App = () => {
    const [currentPage, setCurrentPage] = useState<Page>('HomePage');

    const [filmDetailsPageQueryRef, loadFilmDetailsPageQuery] = useQueryLoader<filmDetailsPageQueryType>(FilmDetailsPageQuery);

    const onDetailsClick = (id: string) => {
        console.log('onDetailsClick ', id);

        try {
            loadFilmDetailsPageQuery({filmId: id});
            setCurrentPage('FilmDetailsPage');
        } catch (e) {
            console.log("e -> ", e);
        }
    };


    return (
        <Scaffolding pageTitle='The Star Wars'>
            <BreadcrumbsNavigation items={getNavigationItems(currentPage)}/>
            {currentPage === 'HomePage' &&
            <HomePage onDetailsClick={onDetailsClick} queryRef={preloadedHomePageQueryRef}/>}
            {currentPage === 'FilmDetailsPage' && filmDetailsPageQueryRef &&
            <FilmDetailsPage queryRef={filmDetailsPageQueryRef}/>}
        </Scaffolding>
    );
}

const AppRoot = (): Node => {

    return <RelayEnvironmentProvider environment={RelayEnvironment}>
        <Suspense fallback={'Loading...'}>
            <App/>
        </Suspense>
    </RelayEnvironmentProvider>;
};

export default AppRoot;