// @flow

import graphql from 'babel-plugin-relay/macro';
import type {GraphQLTaggedNode} from "relay-runtime/query/GraphQLTag";

const HomePageQuery: GraphQLTaggedNode = graphql`
    query homePageQuery {
        allFilms(first: 100) {
            totalCount
            films {
                id
                episodeID
                director
                created
                openingCrawl
                edited
                producers
                title
                releaseDate
            }
        }
    }
`;

export default HomePageQuery;