// @flow

import graphql from 'babel-plugin-relay/macro';
import type {GraphQLTaggedNode} from "relay-runtime/query/GraphQLTag";

const FilmDetailsPageQuery: GraphQLTaggedNode = graphql`
    query filmDetailsPageQuery($filmId: ID!) {
        film(id: $filmId) {
            id
            title
            edited
            created
            ...filmDetailsPage_film_SummaryFragment
            ...filmDetailsPage_film_characterConnectionFragment @arguments(count: 30)
            ...filmDetailsPage_film_planetConnectionFragment @arguments(count: 30)
        }
    }
`;

export const SummaryFragment: GraphQLTaggedNode = graphql`
    fragment filmDetailsPage_film_SummaryFragment on Film {
        episodeID
        director
        producers
        releaseDate
        openingCrawl
    }
`;

export const CharacterConnectionFragment: GraphQLTaggedNode = graphql`
    fragment filmDetailsPage_film_characterConnectionFragment on Film
    @refetchable(queryName: "filmDetailsPage_film_characterConnection_paginationQuery")
    @argumentDefinitions(count: {type: "Int!"}, cursor: {type: "String"}) {
        characterConnection(first: $count, after: $cursor)@connection(key: "filmDetailsPage_film_characterConnection") {
            totalCount
            pageInfo {
                endCursor
                startCursor
                hasNextPage
                hasPreviousPage
            }
            edges {
                cursor
                node {
                    id
                    name
                    species {
                        name
                    }
                    birthYear
                    eyeColor
                    gender
                    hairColor
                    skinColor
                    mass
                    height
                    homeworld {
                        name
                    }

                }
            }
            __typename
        }
    }
`;

export const PlanetConnectionFragment: GraphQLTaggedNode = graphql`
    fragment filmDetailsPage_film_planetConnectionFragment on Film
    @refetchable(queryName: "filmDetailsPage_film_planetConnection_paginationQuery")
    @argumentDefinitions(count: {type: "Int!"}, cursor: {type: "String"}) {
        planetConnection(first: $count, after: $cursor)@connection(key: "filmDetailsPage_film_planetConnection") {
            totalCount
            pageInfo {
                endCursor
                startCursor
                hasNextPage
                hasPreviousPage
            }
            edges {
                cursor
                node {
                    id
                    name
                }
            }
            __typename
        }
    }
`;

export default FilmDetailsPageQuery;