// @flow

import React, {type Node} from 'react';
import Breadcrumbs from '@mui/material/Breadcrumbs';
import Typography from '@mui/material/Typography';
import Link from '@mui/material/Link';

import {Wrapper} from "navigation/styled"

export type NavItemProps = {
    displayText: string,
    isLast: boolean,
};

const NavItem = ({isLast, displayText}: NavItemProps) => {
    return (
        <div>
            {isLast && <Typography color="text.primary">{displayText}</Typography>}
            {!isLast && <Link underline="hover" color="inherit" href="/">
                {displayText}
            </Link>}
        </div>
    );
};

type Props = {
    items: Array<NavItemProps>
};

const BreadcrumbsNavigation = ({items}: Props): Node => {
    return (
        <Wrapper>
            <Breadcrumbs>
                {
                    items.map(
                        ({displayText, isLast}: NavItemProps) => <NavItem key={displayText} isLast={isLast}
                                                                          displayText={displayText}/>
                    )
                }
            </Breadcrumbs>
        </Wrapper>

    );
}

export default BreadcrumbsNavigation;